.modal {
  z-index: 1;
  background-color: #00000080;
  width: 100%;
  height: 100%;
  transition: opacity .3s;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  background-color: var(--background-color);
  border-radius: 10px;
  width: 500px;
  max-width: 90%;
  padding: 40px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 8px #0006;
}

.modal-content button {
  background-color: var(--button-bg-color);
  color: var(--text-color);
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.modal-content button:hover {
  background-color: var(--button-hover-bg-color);
}

#attendance-list h3 {
  margin-bottom: 10px;
}

#attendance-list {
  background-color: #444;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 10px;
}

#attendance-list label {
  color: #ccc;
  text-align: right;
  width: 60px;
  margin-right: 10px;
  display: inline-block;
}

#attendance-list div {
  margin-bottom: 10px;
}

#attendance-list span {
  cursor: pointer;
  border-radius: 5px;
  padding: 5px;
  transition: background-color .3s;
}

#attendance-list span:hover {
  background-color: var(--button-hover-bg-color);
}

.close {
  color: #ccc;
  cursor: pointer;
  font-size: 28px;
  font-weight: bold;
  transition: color .3s;
  position: absolute;
  top: 10px;
  right: 10px;
}

.close:hover, .close:focus {
  color: #fff;
}

#modal select {
  color: #fff;
  background-color: #555;
  border: 1px solid #777;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 16px;
}

#modal select:focus {
  border-color: #fff;
  outline: none;
}

#modal option {
  color: #fff;
  background-color: #444;
}

@media (width <= 700px) {
  .modal-content {
    width: 60%;
  }
}

